.project-card {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 16px 0px;
    padding: 8px 0px;
}
.project-photo{
    width: 40%;
}
.project-info{
    margin-right: 24px;
}
.project-title {
    font-size: 20px;
    font-weight: 600;
}
.project-tags {
    display: flex;
    max-width: 60%;
    flex-wrap: wrap;
}
.tag {
    padding: 4px 8px;
    background-color: lightcyan;
    border-radius: 6px;
    margin-right: 4px;
}
.project-links a{
    text-decoration: none;
    color: black;
}
.project-links {
    margin-top: 6px;
    display: flex;
}
.link-button {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: aquamarine;
    width: max-content;
    padding: 4px 8px;
    border-radius: 6px;
    font-size: 16px;
    margin-right: 6px;
    font-size: 16px;
    margin-right: 6px;
}
.link-button i{
    display: flex;
    align-items: center;
    margin-right: 4px;
}

@media only screen and (max-width: 720px){
    .project-photo {
        width: 70%;
    }
    .project-card {
        flex-direction: column-reverse;
    }
    .project-info{
        margin-left: 0px;
        margin-top: 12px;
    }
    .project-tags{
        max-width: 100%;
        
    }
}